body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.hide {
    display: none;
}

.show {
    padding: 40px;
}
.endpoint-container {
    display: inline-flex;
    flex-direction: column;
    margin: 25px;
    justify-content: space-between;
    width: 70%;
}

.show-btn {
    font-weight: 700;
    height: 67.8125px;
}

.show-btn:active {
    background-color: red;
    color: #fff;
}

.show-btn:focus {
    outline: none;
}

.title-container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.App {
  text-align: center;
}

.endpoint-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.endpoint-title {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 80%;
}
